<script setup lang="ts">
import { ref, computed } from 'vue'
import { ElMessage } from 'element-plus'
import { useUserStore } from '@/stores/user'

const userStore = useUserStore()

const previewData = ref<any>(null)
const activeTab = ref('projects')
const activeCollapse = ref(['1'])
const importing = ref(false)

const uploadHeaders = computed(() => ({
  Authorization: `Bearer ${userStore.token}`
}))

const getStatTitle = (name: string) => {
  const titles: Record<string, string> = {
    projects: 'È°πÁõÆ',
    tasks: '‰ªªÂä°',
    materials: 'Áâ©Êñô',
    procurement: 'ÈááË¥≠',
    departments: 'ÈÉ®Èó®',
    teams: 'Áè≠ÁªÑ',
    users: 'Áî®Êà∑'
  }
  return titles[name] || name
}

const beforeUpload = (file: File) => {
  const isExcel = file.name.endsWith('.xlsx') || file.name.endsWith('.xls')
  const isLt10M = file.size / 1024 / 1024 < 10
  
  if (!isExcel) {
    ElMessage.error('Âè™ÊîØÊåÅ .xlsx Êàñ .xls Êñá‰ª∂')
    return false
  }
  if (!isLt10M) {
    ElMessage.error('Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá 10MB')
    return false
  }
  return true
}

const handleSuccess = (response: any) => {
  previewData.value = response
  if (response.errors?.length > 0) {
    ElMessage.warning(`ÂØºÂÖ•ÂÆåÊàêÔºå‰ΩÜÊúâ ${response.errors.length} ‰∏™ÈîôËØØ`)
  } else {
    ElMessage.success('Êï∞ÊçÆÂØºÂÖ•ÊàêÂäü')
  }
}

const handleError = (error: any) => {
  ElMessage.error(error.message || 'Êñá‰ª∂‰∏ä‰º†Â§±Ë¥•')
}

const confirmImport = async () => {
  importing.value = true
  try {
    // TODO: Ë∞ÉÁî®Á°ÆËÆ§ÂØºÂÖ• API
    await new Promise(resolve => setTimeout(resolve, 1000))
    ElMessage.success('Êï∞ÊçÆÂØºÂÖ•ÊàêÂäü')
    previewData.value = null
  } catch (error: any) {
    ElMessage.error(error.message || 'ÂØºÂÖ•Â§±Ë¥•')
  } finally {
    importing.value = false
  }
}

